import { PhaseType } from "common/types";

export default class DustHelpers {
  static getPhase(pm10: number): PhaseType {
    if (pm10 <= 30) {
      return 1;
    } else if (pm10 <= 80) {
      return 2;
    } else if (pm10 <= 150) {
      return 3;
    } else {
      return 4;
    }
  }

  static getPhaseString(phase: PhaseType) {
    switch (phase) {
      case 1:
        return "좋은";
      case 2:
        return "보통";
      case 3:
        return "나쁨";
      case 4:
        return "매우나쁨";
      default:
        return "";
    }
  }

  static getDescription(phase: PhaseType) {
    switch (phase) {
      case 1:
        return "오늘은 너무 좋은 날이네요.";
      case 2:
        return "보통이지만, 기분은 좋은 날이 되길.";
      case 3:
        return "외출을 하신다면, 마스크가 필요한 날입니다.";
      case 4:
        return "외출을 지양하는게 좋을 것 같아요.";
      default:
        return "";
    }
  }
}



